body {
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "GothamPro", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #414047;
}

body.is-locked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 0;
  overflow: hidden;
}

.container {
  position: relative;
  z-index: 1;
  max-width: 1140px;
  padding: 0 15px;
  margin: 0 auto;
}

.row {
  display: flex;
  margin: 0 -15px;
}

.col {
  padding: 0 15px;
}

.main {
  display: block;
  overflow: hidden;
}

.section {
  position: relative;
  padding: 100px 0 110px;
}

@media only screen and (max-width: 767px) {
  .section {
    padding: 60px 0;
  }
}

.btn {
  position: relative;
  z-index: 1;
  display: inline-block;
  padding: 29px 30px 28px;
  outline: 0;
  border-radius: 50px;
  color: #fff;
  text-align: center;
  box-shadow: -1px 15px 17px 1px rgba(3, 122, 153, 0.12);
  overflow: hidden;
  cursor: pointer;
  font-family: "GothamPro-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1.05px;
}

.btn:hover::after {
  transform: translateY(-50%);
}

.btn::after {
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  z-index: -1;
  height: calc(200% + 4px);
  pointer-events: none;
  background-image: linear-gradient(to top, #3ae1ff 0%, #00afe1 50%, #3ae1ff 100%);
  transition: 0.4s cubic-bezier(0.22, 0.61, 0.36, 1);
  content: '';
}

.btn span {
  position: relative;
  z-index: 1;
}

.title {
  position: relative;
  font-family: "GothamPro-Light", Arial, sans-serif;
  font-weight: normal;
  font-size: 52px;
  line-height: 55px;
  color: #333333;
}

@media only screen and (max-width: 767px) {
  .title {
    font-size: 28px;
    line-height: 32px;
  }
}

.title::after {
  position: absolute;
  width: 120px;
  height: 4px;
  background-color: #3b3a43;
  background-image: linear-gradient(to right, #00afe1 0%, #3ae1ff 100%);
  border-radius: 2px;
  content: '';
}

@media only screen and (max-width: 767px) {
  .title::after {
    width: 60px;
  }
}

.title_white {
  color: #fff;
}

.title_left {
  padding-right: 150px;
}

@media only screen and (max-width: 767px) {
  .title_left {
    padding-right: 70px;
  }
}

.title_left::after {
  top: calc(50% - -4px);
  right: 0;
}

.title_center {
  padding-bottom: 26px;
  text-align: center;
}

@media only screen and (max-width: 767px) {
  .title_center {
    padding-bottom: 15px;
  }
}

.title_center::after {
  bottom: 0;
  left: calc(50% - 60px);
}

@media only screen and (max-width: 767px) {
  .title_center::after {
    left: calc(50% - 30px);
  }
}

.title span {
  display: block;
  margin-left: -8px;
  color: #07c3ea;
  font-family: "Grammatika-Light", Arial, sans-serif;
  font-weight: normal;
  font-size: 72px;
  line-height: 72px;
}

@media only screen and (max-width: 767px) {
  .title span {
    font-size: 34px;
    line-height: 38px;
  }
}

@supports (-webkit-background-clip: text) {
  .title span {
    background-image: linear-gradient(to top, #00afe1 0%, #3ae1ff 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.title-sm {
  font-family: "GothamPro-Medium", Arial, sans-serif;
  font-weight: normal;
  letter-spacing: 1.2px;
  text-transform: uppercase;
}

.title-sm_blue {
  color: #07c3ea;
}

.title-sm_grad {
  color: #07c3ea;
}

@supports (-webkit-background-clip: text) {
  .title-sm_grad {
    background: linear-gradient(to top, #00afe1 0%, #3ae1ff 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.title-sm span {
  font-family: "GothamPro", Arial, sans-serif;
  font-weight: normal;
  text-transform: none;
  letter-spacing: 0;
}

.title-sm p {
  display: inline;
  font-family: "GothamPro", Arial, sans-serif;
  font-weight: normal;
  letter-spacing: 0;
  text-transform: none;
  color: #414047;
}

strong {
  font-family: "GothamPro-Bold", Arial, sans-serif;
  font-weight: normal;
}

.decor-text-bg {
  position: absolute;
  z-index: 1;
  width: 1056px;
  height: 303px;
  pointer-events: none;
  background: url("../img/decor/q-optics_text.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

@media only screen and (max-width: 1023px) {
  .decor-text-bg {
    width: 712px;
    height: 200px;
  }
}

@media only screen and (max-width: 767px) {
  .decor-text-bg {
    display: none;
  }
}

.grad-border {
  position: relative;
}

.grad-border_circle {
  border-radius: 50%;
}

.grad-border::after {
  position: absolute;
  top: -1px;
