  .scheme__row {
    flex-direction: column;
  }
}

.is-ie .scheme__row {
  display: block;
  font-size: 0;
}

.scheme__item {
  position: relative;
  flex: 0 1 33.33333%;
  width: 33.33333%;
  padding: 0 15px;
  color: #333333;
  counter-increment: schemeCount;
}

@media only screen and (max-width: 1439px) {
  .scheme__item {
    flex: 0 1 50%;
    width: 50%;
    max-width: 330px;
  }
}

@media only screen and (max-width: 767px) {
  .scheme__item {
    flex: 0 1 auto;
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }
}

.is-ie .scheme__item {
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
}

.scheme__item::before {
  display: block;
  margin-bottom: 20px;
  color: #07c3ea;
  height: 55px;
  font-family: "GothamPro-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 60px;
  line-height: 60px;
  letter-spacing: 1.72px;
  content: counter(schemeCount, decimal-leading-zero);
}

@supports (-webkit-background-clip: text) {
  .scheme__item::before {
    background-image: linear-gradient(to top, #00afe1 0%, #3ae1ff 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

@media only screen and (max-width: 767px) {
  .scheme__item::before {
    display: inline-block;
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 767px) {
  .scheme__item:nth-child(1) ~ .scheme__item {
    margin-top: 30px;
  }
}

@media only screen and (max-width: 1439px) {
  .scheme__item:nth-child(2) ~ .scheme__item {
    margin-top: 30px;
  }
}

.scheme__item:nth-child(3) ~ .scheme__item {
  margin-top: 30px;
}

